body {
    margin: 0;
    background-color: #f2f2f2;
}

button {
    border: none;
}

.red {
    color: red;
}

.textThrough {
    text-decoration: line-through;
}

.grayBtn {
    background-color: #c8c8c8 !important;
}

.backgroundOrange {
    background-color: #ffa801;
}

.backArrow {
    -webkit-transform: scaleX(-1);
    -ms-transform: scaleX(-1);
        transform: scaleX(-1);
    -webkit-filter: fliph;
            filter: fliph; /*IE*/
}
.hidden{
    display: none;
}


